#user.user-profile #page-header {
  padding-bottom: 10px !important;
}
#user.user-profile #page-header .photo {
  text-align: right;
}
#user.user-profile #page-header .photo img {
  max-height: 250px;
  max-width: 100%;
  box-shadow: 0 0 30px 2px rgba(128, 128, 128, 0.5);
  border-radius: 15px;
}
#user.user-profile #page-header .details {
  display: table;
  text-align: left;
  color: white;
}
#user.user-profile #page-header .details .place {
  font-size: 18px;
  color: inherit;
  text-decoration: underline;
}
#user.user-profile #page-header .details .place:hover {
  color: #fb953e;
}
#user.user-profile #page-header .details .bio {
  font-size: 18px;
}
#user.user-profile #page-header .details .social-profiles {
  display: table-row;
  height: 50px;
}
#user.user-profile #page-header .details .social-profiles h2 {
  margin-top: 0;
  font-size: 16px;
  color: inherit;
}
#user.user-profile #page-header .details .social-profiles a {
  font-size: 30px;
  padding-right: 10px;
  padding-bottom: 10px;
  float: left;
  color: #CCC;
}
#user.user-profile #page-header .details .social-profiles a:hover {
  color: #fb953e;
}
#user #page-header h2,
#user #page-header .function {
  color: white;
  font-size: 23px;
  margin-bottom: 5px;
  margin-top: 10px;
}
#user #page-header h2 i.part-speaker,
#user #page-header .function i.part-speaker {
  font-size: 18px;
}
