.btn.btn-wrap {
  white-space: normal;
}
.panel-title {
  font-weight: bold;
}
.panel-body .checkbox:first-child {
  margin-top: 0;
}
.panel-body ul:last-child {
  margin-bottom: 0;
}
.checkbox label {
  display: block;
}
.checkbox.inline label {
  display: inline-block;
}
ul.piped li {
  display: inline;
}
ul.piped li:not(:last-child):after {
  content: '|';
}
.tooltip-inner {
  margin-bottom: -1px;
}
li.part-participant {
  background-color: forestGreen;
}
li.part-participant i.fa {
  background-color: #0e390e;
}
i.fa.part-participant,
.part-participant i.fa {
  color: forestGreen;
}
i.fa.part-participant:before,
.part-participant i.fa:before {
  content: "\f145";
}
i.fa.part-participant.inverted,
.part-participant i.fa.inverted {
  color: white;
  background-color: forestGreen;
  text-align: center;
  border-radius: 0.25em;
  box-sizing: content-box;
  width: 1em;
  padding: 0.2em 0.25em;
  margin-top: -0.1em;
}
li.part-speaker {
  background-color: dodgerBlue;
}
li.part-speaker i.fa {
  background-color: #005db7;
}
i.fa.part-speaker,
.part-speaker i.fa {
  color: dodgerBlue;
}
i.fa.part-speaker:before,
.part-speaker i.fa:before {
  content: "\f130";
}
i.fa.part-speaker.inverted,
.part-speaker i.fa.inverted {
  color: white;
  background-color: dodgerBlue;
  text-align: center;
  border-radius: 0.25em;
  box-sizing: content-box;
  width: 1em;
  padding: 0.2em 0.25em;
  margin-top: -0.1em;
}
li.part-involved {
  background-color: goldenrod;
}
li.part-involved i.fa {
  background-color: #816213;
}
i.fa.part-involved,
.part-involved i.fa {
  color: goldenrod;
}
i.fa.part-involved:before,
.part-involved i.fa:before {
  content: "\f0c0";
}
i.fa.part-involved.inverted,
.part-involved i.fa.inverted {
  color: white;
  background-color: goldenrod;
  text-align: center;
  border-radius: 0.25em;
  box-sizing: content-box;
  width: 1em;
  padding: 0.2em 0.25em;
  margin-top: -0.1em;
}
li.part-staff {
  background-color: orangeRed;
}
li.part-staff i.fa {
  background-color: #992900;
}
i.fa.part-staff,
.part-staff i.fa {
  color: orangeRed;
}
i.fa.part-staff:before,
.part-staff i.fa:before {
