  border-radius: 0.25em;
  box-sizing: content-box;
  width: 1em;
  padding: 0.2em 0.25em;
  margin-top: -0.1em;
}
[class*=part-] {
  padding-right: 3px;
  font-size: 16px;
  z-index: 11;
}
li[class*=part-] {
  display: inline-block;
  border-radius: 5px;
  padding: 3px 10px 3px 5px;
  margin-bottom: 5px;
  color: white;
}
li[class*=part-] i.fa {
  box-sizing: content-box;
  width: 16px;
  margin: -3px 0 -3px -5px;
  padding: 5px 5px 6px 5px;
  border-right: 1px solid white;
  color: inherit;
  border-radius: 5px 0 0 5px;
  text-align: center;
}
ul.user-stats {
  text-align: center;
  margin-bottom: 15px;
}
.header-inset {
  box-shadow: inset 0 0 25px 7px rgba(0, 0, 0, 0.5);
  color: white;
}
.header-inset.overlay:before {
  background-color: rgba(0, 0, 0, 0.3) !important;
}
.header-inset .tooltip-arrow {
  border-top-color: whitesmoke;
}
.header-inset .tooltip-inner {
  background-color: whitesmoke;
  color: black;
}
.header-inset h1 {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 0;
}
