#event #page-header {
  border: 1px solid #C4C4C4;
  margin-bottom: -1px;
}
#event #page-header .row {
  position: relative;
  margin-bottom: -10px;
}
@media (min-width: 768px) {
  #event #page-header .details {
    text-align: left;
  }
}
#event #page-header .details .status {
  font-style: italic;
}
#event #page-header .details .place {
  font-size: 18px;
  color: inherit;
  text-decoration: underline;
}
#event #page-header .details .place:hover {
  color: #fb953e;
}
#event #page-header .details .function {
  font-size: 23px;
  margin-bottom: 5px;
  margin-top: 10px;
}
#event #page-header .details .function i.part-speaker {
  font-size: 18px;
}
#event #page-header .details .links {
  margin-bottom: 0;
}
#event #page-header .details .links a {
  color: inherit;
}
#event #page-header .details .links a:not(:last-child) {
  padding-right: 10px;
}
#event #page-header .details .links a:hover {
  color: #3758a9;
}
#event #page-header .stats {
  margin-top: 10px;
  display: inline-block;
}
@media (min-width: 768px) {
  #event #page-header .stats {
    margin-top: 0;
    position: absolute;
    text-align: right;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}
#event #page-header .stats p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 16px;
  margin-bottom: 10px;
}
#event #page-header .stats p:last-child {
  margin-bottom: 0;
}
#event #page-header .stats hr {
  border-top-color: rgba(255, 255, 255, 0.4);
  margin: 10px 0;
}
#event #page-header + .nav {
  padding-left: 15px;
}
#event .section-title {
  margin-bottom: 20px;
}
#event ul.materials li {
  padding-bottom: 10px;
}
#event ul.materials li:before {
  font-family: FontAwesome;
  display: inline-block;
  width: 20px;
  text-align: center;
}
#event ul.materials li.doc:before {
  content: "\f0f6";
}
#event ul.materials li.pdf:before {
  content: "\f1c1";
}
#event ul.materials li.zip:before {
  content: "\f1c6";
}
#event ul.materials li.slide:before {
  content: "\f26c";
}
#event ul.materials li.audio:before {
  content: "\f1c7";
}
#event ul.materials li.video:before {
  content: "\f1c8";
}
