label sup {
  color: #ae5000;
  font-size: 200%;
  top: 0.25em;
}
.form-group .help-block,
.form-group label.error {
  font-weight: normal;
  font-style: italic;
  font-size: 80%;
}
.form-group.error-block label:not(.error) {
  color: #ae5000;
}
.form-group.error-block input,
.form-group.error-block .input-group-addon {
  border-color: #ae5000;
}
